.addUser form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}
.addUser div {
  flex: 1 20%;
  padding: 2%;
}

.addUser label,
.addUser input,
.addUser button,
.addUser select {
  font-size: 1.5rem;
  padding: 10px;
  width: 100%;
}

.error {
  color: red;
}

.error svg {
  padding-right: 10px;
}

.success {
  color: green;
}

.success svg {
  padding-right: 10px;
}
